.courseInfo{
    height: 100%;
    font-family: 'Raleway', sans-serif;
    margin: 0;
    padding: 0;
}

.dark-courseInfo *{
    color: rgba(255,255,255,1);
}

.courseInfo h2 {
    background-image: linear-gradient(to right bottom, red,orange,yellow);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}

.countdown{
    box-shadow: inset 0 0 .3rem rgba(0,0,0,.3);
    border-radius: .6rem;
}

.details{
    box-shadow: inset 0 0 .3em rgba(0,0,0,.5);
    border-radius: .8rem;
}

.dark-courseInfo .countdown{
    box-shadow: inset 0 0 .3rem rgba(250,250,250,.3);
}

.dark-courseInfo .details{
    box-shadow: inset 0 0 .5rem rgba(250,250,250,.5);
}